import React, { Component } from 'react'
import { FaAlignRight } from "react-icons/fa";
import {Link} from 'react-router-dom';

export class Nav extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
        isOpen:false,
        isSticky:false
    }
  }

   toggle = () =>{
     this.setState({
       isOpen:!this.state.isOpen
     })
   }
 
 
   sticky = () => {
    if(window.scrollY > 0){
      this.setState({
        isSticky:true
      })
    }else{
      this.setState({
        isSticky:false
      })
    }

  }

  top = () => {
    this.setState({
      isOpen: false
    });
    return window.scrollTo(0, 0);
  };
  
  render() {
    window.addEventListener('scroll',  this.sticky)


    return (
      <>
      <div className="nav-bg"> </div>
      <div className={this.state.isSticky ? "nav-top nav-sticky" : "nav-top"} >
        <header className="nav-header">
          <span className="logo">
            <strong>Web</strong>Experts
          </span>
          <button type="button" className="nav-btn" onClick={this.toggle} >
            <FaAlignRight className="nav-icon" />
          </button>
         
        </header>

        <ul className={this.state.isOpen ? "show-nav" : "nav"}>
          <Link to='/React-webexperts' onClick={this.top}>
          <li className="nav-links" >
            <a href="/React-webexperts"  >HOME</a>
          </li>
          </Link>

          <Link to='/services' onClick={this.top} >
          <li className="nav-links">
            <a href="/services" >SERVICES</a>
          </li>
          </Link>

            <Link to='/portfolios' onClick={this.top}>
          <li className="nav-links">
            <a href="/portfolios"  >PORTFOLIO</a>
          </li>
          </Link>
          <Link to='/about' onClick={this.top}>
          <li className="nav-links">
            <a href="/about">ABOUT</a>
          </li>
          </Link>

          <Link to='/Blogs' onClick={this.top}>
          <li className="nav-links">
            <a href="/Blogs"  >BLOGS</a>
          </li>
          </Link>

          <Link to='/React-webexperts' onClick={this.top}>
          <li className="nav-links">
          <a href="/" >CONTACT</a>
          </li>
          </Link>
        </ul>
        <button className="login-btn"><a href="/React-webexperts">Log in</a></button>
      </div>
    </>
    )
  }
}

export default Nav
